<template lang="">
    <ion-page>
        <ion-header>
            <ion-toolbar>

                <ion-title style="font-weight: 600; font-size: 25px;">Patient Details</ion-title>


            </ion-toolbar>
        </ion-header>

        <ion-content>
            <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600;">Patient Name</ion-label>
                <ion-label>{{ `${details.first_name } ${details.middle_name } ${details.last_name }`}}</ion-label>
            </ion-item>
            <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600;">Patient No</ion-label>
                <ion-label>{{ details.client_id }}</ion-label>
            </ion-item>
            <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600;">External Patient No</ion-label>
                <ion-label>{{ details.uuid }}</ion-label>
            </ion-item>
            <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600;">Date of Birth</ion-label>
                <ion-label>{{ details.date_of_birth }}</ion-label>
            </ion-item>
            <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600;">Sex</ion-label>
                <ion-label>{{ details.sex }}</ion-label>
            </ion-item>
            <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600;">Physical Address</ion-label>
                <ion-label>{{ details.physical_address }}</ion-label>
            </ion-item>
            <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600;">Phone Number</ion-label>
                <ion-label>{{ details.phone_number }}</ion-label>
            </ion-item>
            <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600;">Date Created</ion-label>
                <ion-label>{{ moment(details.created_at).format('DD/MMMM/YYYY HH:mm') }}</ion-label>
            </ion-item>
        </ion-content>
        <ion-footer>
            <ion-toolbar color="light" style="padding: 5px;">

                <ion-button size="large" color="success" slot="start" @click="goBack()">
                    Back
                </ion-button>

                <ion-button @click="viewOrders(details.client_id)" color="primary" fill="solid" size="large" slot="end">
                    View Orders
                </ion-button>
                <ion-button color="primary" fill="solid" size="large" slot="end">
                    Edit
                </ion-button>
                <ion-button color="success" fill="solid" size="large" slot="end">
                    New Test
                </ion-button>
            </ion-toolbar>
        </ion-footer>
    </ion-page>
</template>

<script lang="ts">

import { Client } from "@/composables/client";
import {
    IonPage,
    IonLabel,
    IonItem,
    IonTitle,
    IonToolbar,
    IonHeader,
    IonButton,
    IonFooter,
    IonContent} from "@ionic/vue"
import { defineComponent } from "vue";

import moment from "moment";
import router from "@/router";

export default defineComponent({
    components: {
        IonPage,
        IonLabel,
        IonItem,
        IonTitle,
        IonToolbar,
        IonHeader,
        IonButton,
        IonFooter,
        IonContent,
    },
    created(){
        this.init();
    },
    data(){

        return{
            details: {} as any,
            moment: moment
        }
    },
    methods: {
        goBack () : void {
            router.back();
        },
        async init () : Promise<void> {

            const client = Client();

            const res = await client.getJson(`clients/${this.$route.params.id}`);

            this.details = res?.data

        },
        viewOrders(id: number) : void {
            this.$router.push(`/tests?patient_id=${id}`);
        }
    }
})
</script>

